<template>
	<div>
  <div class="page-title">
    <h3>Новая запись</h3>
  </div>

  <Loader v-if="isLoading"></Loader>

  <form v-else class="form" @submit.prevent="submitHanler">
    <div class="input-field" >
      <select ref="select" v-model="category">
        <option
          v-for="cat in categories"
          :key="cat.id"
          :value="cat.id"
        >{{cat.title}}</option>
      </select>
      <label>Выберите категорию</label>
    </div>

    <p>
      <label>
        <input
            class="with-gap"
            name="type"
            type="radio"
            value="income"
            v-model="recordType"
        />
        <span>Доход</span>
      </label>
    </p>

    <p>
      <label>
        <input
            class="with-gap"
            name="type"
            type="radio"
            value="outcome"
            v-model="recordType"
        />
        <span>Расход</span>
      </label>
    </p>

    <div class="input-field">
      <input
          id="amount"
          type="number"
          v-model.number="limit"
          :class="{invalid: $v.limit.$dirty && !$v.limit.minValue}"
      >
      <label for="amount">Сумма</label>
      <span class="helper-text invalid"
        v-if="$v.limit.$dirty && !$v.limit.minValue">должно быть не менее 1</span>
    </div>

    <div class="input-field">
      <input
          id="description"
          type="text"
          v-model="description"
          :class="{invalid: $v.description.$dirty && !$v.description.required}"
      >
      <label for="description">Описание</label>
      <span
            class="helper-text invalid"
            v-if="$v.description.$dirty && !$v.description.required">Введите описание</span>
    </div>

    <button class="btn waves-effect waves-light" type="submit">
      Создать
      <i class="material-icons right">send</i>
    </button>
  </form>
</div>
</template>

<script>
  import {required, minValue} from 'vuelidate/lib/validators'
  import {mapGetters} from 'vuex'

  export default{
    data(){
      return{
        categories: [],
        description: '',
        limit: 1,
        recordType: 'outcome',
        category: '',
        isLoading: true
      }
    },
    watch: {
      category(value){
        console.log(value);
      }
    },
    async mounted(){
       this.categories = await this.$store.dispatch('getCategories')

       this.isLoading = false

       M.updateTextFields()
       setTimeout(() => {
        this.select = M.FormSelect.init(this.$refs.select);

       }, 0)
    },
    destroyed(){
      if(this.select && this.select.destroy){
        this.select.destroy()
      }
    },
    validations: {
      description: {required},
      limit: {minValue: minValue(1)}
    },
    computed:{
     ...mapGetters(['info']),//импорт геттера через mapGetters делает его обычным
        //computed-свойством, доступным в любом месте кода внутри компонента в виде "this.info"
     isPossible(){
        if(this.recordType === 'outcome'){
          const userBill = this.info.bill
          return userBill >= this.limit ? userBill : false  
        }
        return this.info.bill
      }
    },
    methods: {
     async submitHanler(){
        if(this.$v.$invalid){
          this.$v.$touch()
          return
        }
        if(!this.isPossible){
          this.$message(`Не хватает ${this.limit - this.info.bill} для совершения операции` )
          return
        }else{
          try{
            const recordData = {
              categoryId: this.category,
              description: this.description,
              amount: this.limit,
              date: new Date().toJSON(),
              type: this.recordType
            }
            // console.log('this.limit ' + typeof this.limit);
            const recorder = await this.$store.dispatch('createRecord', recordData)

            // this.$message('Категоря создана')
            const bill = this.recordType === 'income' ? this.info.bill + this.limit : this.info.bill - this.limit

            console.log(recorder);

            await this.$store.dispatch('updateInfo', {bill}) //передаем объект со свойством, которое нужно обновить в бвзе
            this.$message(`Запись "${this.description}" успешно создана`)

            this.$v.$reset() //очищаем валидацию формы

            this.description = ''
            this.limit = 1

          }catch(e){

          }
          
        }
      }
    }
  }
</script>